

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import { Directionality } from '@angular/cdk/bidi';
import { isTooltipEmpty, TDSTooltipBaseComponent, TDSTooltipBaseDirective, TDSTooltipTrigger, TDSPropertyMapping } from './base';
import { NgClassInterface, NgStyleInterface, TDSTSType } from 'tds-ui/core/config';
import { zoomBigMotion } from 'tds-ui/core/animation';
import { TDSHelperObject } from 'tds-ui/shared/utility';

@Directive({
  selector: '[tds-tooltip]',
  exportAs: 'tdsTooltip',
  host: {
    '[class.tds-tooltip-open]': 'visible'
  }
})
export class TDSTooltipDirective extends TDSTooltipBaseDirective {
  @Input('tooltipTitle') title?: TDSTSType | null;
  @Input('tds-tooltip') directiveTitle?: TDSTSType | null;
  @Input('tooltipTrigger') trigger?: TDSTooltipTrigger = 'hover';
  @Input('tooltipPlacement') placement?: string | string[] = 'top';
  @Input('tooltipOrigin') origin?: ElementRef<HTMLElement>;
  @Input('tooltipVisible') visible?: boolean;
  @Input('tooltipMouseEnterDelay') mouseEnterDelay?: number;
  @Input('tooltipMouseLeaveDelay') mouseLeaveDelay?: number;
  @Input('tooltipOverlayClassName') overlayClassName?: string;
  @Input('tooltipOverlayStyle') overlayStyle?: NgStyleInterface;
  @Input() tooltipColor? : 'info' | 'success' | 'error' | 'warning';
  @Input() tooltipContentCss?: NgClassInterface |string ;
  @Input() tooltipArrowCss?: NgClassInterface|string ;
  // tslint:disable-next-line:no-output-rename
  @Output('tooltipVisibleChange') readonly visibleChange = new EventEmitter<boolean>();

  override componentRef: ComponentRef<TDSToolTipComponent> = this.hostView.createComponent(TDSToolTipComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    // @Host() @Optional() noAnimation?: TDSNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer);
  }

  protected getProxyPropertyMap(): TDSPropertyMapping {
    return {
      tooltipCss: ['tooltipContentCss', () => this.tooltipContentCss],
      tooltipArrowCss: ['tooltipArrowCss', () => this.tooltipArrowCss],
      tooltipColor:['color', () => this.tooltipColor]
    };
  }
}

@Component({
  selector: 'tds-tooltip',
  exportAs: 'TDSTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [zoomBigMotion],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayPush]="true"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="tds-tooltip  p-1.5 relative text-white text-body-2 font-regular"        
        [ngClass]="_classMap"
        [ngStyle]="overlayStyle"     
        [@zoomBigMotion]="'active'"
      >
        <div class="tds-tooltip-content p-1 relative">
          <div class="tds-tooltip-arrow absolute block bg-transparent pointer-events-none"
          [ngStyle]="_contentStyleArrow" >
            <span class="tds-tooltip-arrow-content block" [ngClass]='_tooltipArrowCss' [ngStyle]="_contentStyleArrowContent"></span>
          </div>
          <div class="tds-tooltip-inner" [ngClass]="_tooltipCss">
            <ng-container *tdsStringTemplateOutlet="title">{{ title }}</ng-container>
          </div>
        </div>
      </div>
    </ng-template>
  `,
  preserveWhitespaces: false
})
export class TDSToolTipComponent extends TDSTooltipBaseComponent {
  title: TDSTSType | null = null;

  color?: 'info' | 'success' | 'error' | 'warning';
  tooltipContentCss: NgClassInterface | null = null;
  tooltipArrowCss: NgClassInterface | null = null;
  _tooltipCss: NgClassInterface = {};
  _tooltipArrowCss: NgClassInterface = {};
  _contentStyleMap: NgStyleInterface = {};
  _contentStyleArrowContent: NgStyleInterface = {};
  _contentStyleArrow: NgStyleInterface = {}
  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    // @Host() @Optional() public noAnimation?: TDSNoAnimationDirective
  ) {
    super(cdr);
  }

  protected isEmpty(): boolean {
    return isTooltipEmpty(this.title);
  }

  updateStyles(): void {
    const isColorPreset = this.color;

    this._classMap = {
      [this.overlayClassName]: true,
      [`${this._prefix}-placement-${this.preferredPlacement}`]: true,

    };
    if (TDSHelperObject.hasValue(this.color)) {
      this.getCssByColor();
    } else {
      //lấy mặc định or custom
      this._tooltipCss = this.tooltipContentCss || {
        ['bg-black bg-opacity-80 dark:bg-d-neutral-3-500 dark:bg-opacity-100 rounded p-2']: true,

      };
      this._tooltipArrowCss = this.tooltipArrowCss || {
        ' border-black  border-opacity-80 dark:border-d-neutral-3-500 dark:border-opacity-100 border-4': true
      }
    }
    this.p_StyleArrow();
  }
  private p_StyleArrow() {
    let borderTransparent = '4px solid transparent';
    // let borderColor = '4px solid';
    if (this.preferredPlacement.indexOf("top") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderLeft: borderTransparent,
        borderRight: borderTransparent,
        borderBottom: 0
        // borderTop: borderColor,
      }
    }
    if (this.preferredPlacement.indexOf("bottom") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderLeft: borderTransparent,
        borderRight: borderTransparent,
        borderTop: 0
        // borderBottom: borderColor,        
      }
    }
    if (this.preferredPlacement.indexOf("left") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderTop: borderTransparent,
        // borderLeft : borderColor,
        borderBottom: borderTransparent,
        borderRight: 0
      }
    }
    if (this.preferredPlacement.indexOf("right") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderTop: borderTransparent,
        // borderRight : borderColor,
        borderBottom: borderTransparent,
        borderLeft: 0
      }
    }
    switch (this.preferredPlacement) {
      //#region top
      case "top":
        this._contentStyleArrow = {
          bottom: '0',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
      case "topLeft":
        this._contentStyleArrow = {
          bottom: '0',
          left: '10px',
        }
        break;
      case "topRight":
        this._contentStyleArrow = {
          bottom: '0',
          right: '10px',
        }
        break;
      //#endregion
      //#region bottom
      case "bottom":
        this._contentStyleArrow = {
          top: '0',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
      case "bottomLeft":
        this._contentStyleArrow = {
          top: '0',
          left: '10px',
        }
        break;
      case "bottomRight":
        this._contentStyleArrow = {
          top: '0',
          right: '10px',
        }
        break;
      //#endregion
      //#region left
      case "left":
        this._contentStyleArrow = {
          right: '0px',
          top: '50%',
          transform: 'translateY(-50%)',
        }
        break;
      case "leftTop":
        this._contentStyleArrow = {
          right: '0px',
          top: '10px'
        }
        break;
      case "leftBottom":
        this._contentStyleArrow = {
          right: '0px',
          bottom: '10px'
        }
        break;
      //#endregion
      //#region right
      case "right":
        this._contentStyleArrow = {
          left: '0px',
          top: '50%',
          transform: 'translateY(-50%)',
        }
        break;
      case "rightTop":
        this._contentStyleArrow = {
          left: '0px',
          top: '10px'
        }
        break;
      case "rightBottom":
        this._contentStyleArrow = {
          left: '0px',
          bottom: '10px'
        }
        break;
      ////#endregion
      default:
        this._contentStyleArrow = {
          bottom: '0',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
    }

  }
  private getCssByColor() {
    let contentCss: string = '';
    let arrowContent: string = '';

    switch (this.color) {
      case 'info':
        contentCss = "bg-info-400 dark:bg-d-info-400 p-2 rounded";
        arrowContent = 'border-4 border-info-400 dark:border-d-info-400';
        break
      case 'success':
        contentCss = "bg-success-400 dark:bg-d-success-400 p-2 rounded";
        arrowContent = 'border-4 border-success-400 dark:border-d-success-400';
        break
      case 'error':
        contentCss = "bg-error-400 dark:bg-d-error-400 p-2 rounded";
        arrowContent = 'border-4 border-error-400 dark:border-d-error-400';
        break
      case 'warning':
        contentCss = "bg-warning-400 dark:bg-d-warning-400 p-2 rounded";
        arrowContent = 'border-4 border-warning-400 dark:border-d-warning-400';
        break
    }
    this._tooltipCss = {
      [contentCss]: true
    };
    this._tooltipArrowCss = {
      [arrowContent]: true
    }


  }
}
